(function (angular, Constants) {
    'use strict';
    var ThemesFilter = /** @class */ (function () {
        function ThemesFilter($element, $http) {
            this.$el = $element;
            this.$http = $http;
        }
        ThemesFilter.prototype.$onInit = function () {
            this.collapse = true;
            this.query = {
                type: 'skos:Concept',
                fields: 'scheme',
                size: 20,
                sort: 'label,asc'
            };
            this.updateValues();
        };
        ThemesFilter.prototype.$onDestroy = function () {
            this.values = null;
            this.service = null;
            this.$el = null;
            this.$http = null;
        };
        /**
         * @return {array[object]} currently selected themes
         */
        ThemesFilter.prototype.getSelected = function () { return this.service.getThemes() || []; };
        /**
         * @return {boolean} true if any themes are selected
         */
        ThemesFilter.prototype.hasSelections = function () { return this.getSelected().length; };
        /**
         * @param {object} value - theme being checked
         * @return {boolean} true if specified theme is selected
         */
        ThemesFilter.prototype.isSelected = function (value) { return ~this.getSelected().indexOf(value.id); };
        /**
         * @param {object} value - theme being toggled selected/unselected
         */
        ThemesFilter.prototype.toggle = function (value) {
            var themes = this.getSelected();
            var idx = themes.indexOf(value.id);
            if (idx >= 0)
                themes.splice(idx, 1);
            else
                themes.push(value.id);
            this.service.setThemes(themes);
        };
        /**
         * @param {object} value - theme being toggled selected/unselected
         */
        ThemesFilter.prototype.deselectOutside = function (value) {
            //remove selected item that is outside current filtered set of options
            this.outsideResults = (this.outsideResults || []).filter(function (v) { return v.id !== value.id; });
            this.toggle(value);
        };
        /**
         * Remove all selected themes
         */
        ThemesFilter.prototype.clear = function () {
            var themes = this.getSelected();
            if (!themes || !themes.length)
                this.collapse = !this.collapse; //toggle collapsed state
            else
                this.service.setThemes([]);
        };
        /**
         * @param {object} value - theme
         * @return {integer} facet count for specified theme
         */
        ThemesFilter.prototype.getCount = function (value) {
            var facet = this.service.getFacet('themes');
            if (!facet)
                return '';
            var valObj = facet.buckets.find(function (v) { return v.label === value.id; });
            if (!valObj)
                return '';
            return valObj.count;
        };
        /**
         * @param {object} scheme - concept scheme to constrain theme options using
         */
        ThemesFilter.prototype.onSchemeChange = function (scheme) {
            if (scheme)
                this.query['scheme.id'] = scheme.id;
            else
                delete this.query['scheme.id'];
            this.updateValues();
        };
        /**
         * @param {string} keywords - text query to constrain theme options using
         */
        ThemesFilter.prototype.onKeywordsChange = function (keywords) {
            if (keywords && keywords.length)
                this.query.q = keywords;
            else
                delete this.query.q;
            this.updateValues();
        };
        /**
         *
         */
        ThemesFilter.prototype.updateValues = function () {
            var _this = this;
            var params = this.query;
            params.bust = new Date().getTime();
            return this.$http.get(Constants.ualUrl + '/api/items', { params: params })
                .then(function (response) {
                var total = response.data.totalResults;
                var newValues = response.data.results.slice(0);
                _this.additionalValueCount = total - newValues.length;
                var selections = _this.getSelected();
                _this.outsideResults = (_this.values || []).filter(function (v) {
                    //find existing values that are selected
                    return ~selections.indexOf(v.id) &&
                        // but not in new set of values
                        !newValues.filter(function (nv) { return nv.id === v.id; }).length;
                });
                _this.values = newValues;
            }, function (response) {
                console.log("(" + response.status + ") " + response.statusText);
            }).catch(function (e) {
                console.log("Error fetching NGDA Themes: " + e.message);
            });
        };
        /**
         *
         */
        ThemesFilter.prototype.updateSchemes = function () {
            var _this = this;
            this.fetchSchemes()
                .then(function (schemes) { _this.conceptSchemes = schemes; })
                .catch(function (e) {
                console.log("Error loading available concept schemes " + e.message);
            });
        };
        /**
         * @param {string} q - query to constrain schemes using
         */
        ThemesFilter.prototype.fetchSchemes = function (q) {
            var _this = this;
            this.areSchemesLoading = true;
            var params = { type: 'skos:ConceptScheme', size: 12, sort: 'label,asc' };
            if (q && q.length)
                params.q = q;
            return this.$http.get(Constants.ualUrl + '/api/items', { params: params })
                .then(function (response) {
                var hits = response.data.results;
                _this.noSchemeResults = !hits.length;
                return hits;
            })
                .finally(function () { _this.areSchemesLoading = false; });
        };
        /**
         * @param {object} $item - scheme selected
         * @param {object} $model - scheme selected
         * @param {string} $label -
         * @param {object} $event - selection event
         */
        ThemesFilter.prototype.onSchemeSelection = function ($item, $model, $label, $event) {
            this.onSchemeChange($item);
            this.$el.find('input[name="theme-filter-schemes"]').blur();
        };
        return ThemesFilter;
    }());
    angular.module('gp-common').component('themeFilter', {
        bindings: {
            name: '@',
            service: '<'
        },
        controller: ThemesFilter,
        template: "\n            <div class=\"card c-query-filter c-browse-filter\">\n                <div class=\"card-title\">\n                    <button type=\"button\" class=\"btn btn-sm btn-link\"\n                        title=\"{{$ctrl.collapse?'Expand':'Collapse'}}\"\n                        ng-click=\"$ctrl.collapse = !$ctrl.collapse\">\n                        <span class=\"glyphicon\" ng-class=\"{'glyphicon-minus':!$ctrl.collapse,'glyphicon-plus':$ctrl.collapse}\"></span>\n                    </button>\n                    <span>Filter by Themes</span>\n                </div>\n                <div class=\"card-content\">\n                    <div class=\"c-facets\" ng-class=\"{'is-collapsed':$ctrl.collapse}\">\n\n                        <div class=\"c-facet__value\">\n                            <div class=\"input-group-slick\">\n                                <input name=\"theme-filter-schemes\" type=\"text\" class=\"form-control\"\n                                    ng-model=\"$ctrl.scheme\"\n                                    typeahead-on-select=\"$ctrl.onSchemeSelection($item, $model, $label, $event)\"\n                                    uib-typeahead=\"opt as opt.label for opt in $ctrl.fetchSchemes($viewValue)\"\n                                    typeahead-loading=\"$ctrl.areSchemesLoading\"\n                                    typeahead-no-results=\"$ctrl.noSchemeResults\"\n                                    ng-model-options=\"{ debounce: 250 }\"\n                                    typeahead-min-length=\"2\"\n                                    typeahead-editable=\"false\"\n                                    placeholder=\"Filter Themes by Scheme...\"\n                                    aria-label=\"Find scheme by name to filter theme options\">\n                                <span class=\"glyphicon glyphicon-remove\"\n                                    title=\"Clear selected Scheme\"\n                                    ng-if=\"$ctrl.scheme\"\n                                    ng-click=\"$ctrl.onSchemeChange($ctrl.scheme=null)\">\n                                </span>\n                            </div>\n                        </div>\n\n                        <div class=\"c-facet__value\">\n                            <div class=\"input-group-slick\">\n                                <input name=\"scheme-typeahead\" type=\"text\" class=\"form-control\"\n                                    ng-model=\"$ctrl.typeaheadValue\"\n                                    ng-change=\"$ctrl.onKeywordsChange($ctrl.typeaheadValue)\"\n                                    ng-model-options=\"{debounce:200}\"\n                                    placeholder=\"Find a Theme by name...\"\n                                    aria-label=\"Find a theme by name\">\n                                <span class=\"glyphicon glyphicon-remove\"\n                                    title=\"Clear query\"\n                                    ng-if=\"$ctrl.typeaheadValue.length\"\n                                    ng-click=\"$ctrl.onKeywordsChange($ctrl.typeaheadValue=null)\">\n                                </span>\n                            </div>\n                        </div>\n\n                        <a class=\"c-facet__value\" ng-click=\"$ctrl.clear()\"\n                            ng-class=\"{active:!$ctrl.hasSelections()}\">\n                            <span class=\"glyphicon\"\n                                ng-class=\"{'glyphicon-check':!$ctrl.hasSelections(), 'glyphicon-unchecked t-fg--gray-lt':$ctrl.hasSelections()}\">\n                            </span>\n                            Any Theme\n                        </a>\n                        <a ng-repeat=\"theme in $ctrl.values track by $index\"\n                            class=\"c-facet__value\" ng-click=\"$ctrl.toggle(theme)\"\n                            ng-class=\"{active:$ctrl.isSelected(theme)}\">\n                            <span class=\"badge pull-right\">{{$ctrl.getCount(theme)}}</span>\n                            <span class=\"glyphicon\"\n                                ng-class=\"{'glyphicon-check':$ctrl.isSelected(theme),'glyphicon-unchecked t-fg--gray-lt':!$ctrl.isSelected(theme)}\"></span>\n                            {{theme.label || \"Untitled Theme\"}}\n                        </a>\n\n                        <div class=\"c-facet__value disabled t-fg--gray-md\"\n                            ng-if=\"$ctrl.additionalValueCount\">\n                            <em>plus {{$ctrl.additionalValueCount}} more options</em>\n                        </div>\n\n\n                        <div class=\"c-facet__value disabled\" ng-if=\"$ctrl.outsideResults.length\">\n                            <em>The following selections are not in the above results</em>\n                        </div>\n\n                        <a ng-repeat=\"theme in $ctrl.outsideResults track by $index\"\n                            class=\"c-facet__value active\" ng-click=\"$ctrl.deselectOutside(theme)\">\n                            <span class=\"badge pull-right\">{{$ctrl.getCount(theme)}}</span>\n                            <span class=\"glyphicon glyphicon-check\"></span>\n                            {{theme.label || \"Untitled Theme\"}}\n                        </a>\n\n                    </div>\n                </div>\n            </div>\n        "
    });
})(angular, GeoPlatform);
